@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.filters {
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
}

.filters > label {
  color: #fff;
}
.searchbar {
  width: 255px;
  height: 65%;
  padding-left: 5px;
  margin: 0 5px 0 5px;
  outline: none;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  font-size: 16px;
  transition: box-shadow 0.6s linear;
}

.searchbar:focus {
  box-shadow: 0px 0px 0px 5px #878b8b;
}

.searchbar::-webkit-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchbar:-ms-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchbar::placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchmenu {
  display: flex;
  flex-direction: column;
  width: 245px;
  padding: 15px 5px;
  position: absolute;
  top: 40px;
  left: 60px;
  background-color: #fff;
  line-height: 40px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  opacity: 0;
}

.searchbar:focus + .searchmenu {
  animation: slide-down 0.5s ease-out;
  opacity: 1;
}

.clear {
  padding: 10px;
  margin: 2px;
  border: none;
  border-radius: 10px;
}

.clear:hover {
  box-shadow: 0px 0px 0px 3px #878b8b;
}

@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#header-component {
  height: 48px;
  background-color: #363c3c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

#header-component > a {
  font-size: 2em;
  font-weight: bolder;
  color: #fff;
  text-decoration: none;
  margin: 0 auto 0 25px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-bottom: 25px;
}

.flex-container {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.25);
}

.flex-container:nth-child(odd) {
  margin: 0 0 0 5px;
}

.flex-container:nth-child(even) {
  margin: 0 5px 0 0;
}

.owner {
  display: flex;
}

.owner > h2 {
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 700;
}
.post-info {
  margin-left: 15px;
}

.post-info > div {
  display: flex;
  flex-direction: row;
}

.post-image {
  object-fit: contain;
  max-width: 100%;
  height: 300px;
}

.image-subtext {
  display: flex;
  justify-content: space-between;
  padding-right: 15px;
}

.tagbar {
  display: flex;
  flex-wrap: wrap;
}

.tagbar > p {
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 5px;
  margin: 2px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.1);
}

.top {
  padding: 20px;
  text-transform: uppercase;
  color: #000;
  text-shadow: #c0c0c0 1px 0 5px;
  background-color: transparent;
  border: #000 1px solid;
  border-radius: 5px;
  transition: 0.8s;
  position: relative;
  overflow: hidden;
  outline: none;
}

.top:hover {
  color: #fff;
}

.top::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0%;
  background-color: #000;
  z-index: -1;
  transition: 0.8s;
  border-radius: 50% 50% 0 0;
}

.top:hover::before {
  height: 180%;
}

.thumbnail {
  display: flex;
  justify-content: center;
  padding: 0 10px 0 0;
}

.thumbnail > img {
  height: 50px;
  width: 50px;
  margin: auto;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 100%;
}

.range-wrap {
  color: #fff;
  display: flex;
  position: relative;
  margin: 0 15px 0 0;
}
.range {
  width: 100%;
}
.bubble {
  opacity: 0;
  background: #000;
  color: #fff;
  padding: 2px 4px;
  position: absolute;
  border-radius: 4px;
  left: 50%;
  transform: translateX(-50%);
}
.bubble::after {
  content: "";
  position: absolute;
  width: auto;
  height: 2px;
  background: #000;
  top: -1px;
  left: 50%;
}

.show {
  opacity: 1;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 20px;
  border: none;
  outline: none;
  color: #fff;
  padding: 12px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.dropdown:hover .dropbtn {
  color: #878b8b;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown:hover .dropdown-content {
  animation: slide-down 0.5s ease-out;
  display: block;
}

.grid-column {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

